import React from 'react';
import bgHome from '../../img/swoop-05.svg';
import './Main.css';
import { useNavigate } from 'react-router-dom/dist';

const Main = () => {
  const navigate = useNavigate()
  return (
    <div style={{
      backgroundImage: `url(${bgHome}), linear-gradient(to bottom, #3498db, #ffffff)`,
      backgroundSize: 'cover',
      minHeight: '100vh',
      display: 'flex',
    }}>
       <div className="container">
        <div className="containerform">
          <h1 className='mainH1'>GrazeMind -  </h1>
          <p className='mainP'>GrazeMind -      .  <br />      <br />       . <br />       , <br />        . </p>
          <h2 className='mainH2'>GrazeMind -       .</h2>
          <button className='mainBtn' onClick={() => navigate('/about')}> </button>
        </div>
        <div className="containerforml">
        <iframe className='video' width="490" height="380" src="https://youtu.be/jMgx8Z-9cqQ" title=" - G-List     " frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
        </div>
       </div>
    </div>
  );
};

export default Main;
